<template>
  <input placeholder="Type something to search for awesome gifs" v-model="keyword" @input="onInput">
</template>

<script>

export default {
  name: 'Search',
  data() {
    return {
      keyword: '',
      timeout: null
    }
  },
  methods: {
    onInput() {
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        this.search();
      }, 1000);
      },
    search() {
      fetch(`https://api.giphy.com/v1/gifs/search?api_key=lzG0huuBkF4JawJfdGTyEYplEYgwR7n6&q=${this.keyword}&limit=9`)
      .then(reponse => reponse.json())
      .then(result => {
        console.log(result)
        this.$emit('fetch-gifs', result.data);
      })
    }
  },
}
</script>

<style scoped>
  input {
    padding: 10px 16px;
    border-radius: 4px;
    font-size: 18px;
    outline: none;
    border: 2px solid rgb(102, 102, 102);
    display: block;
    width: 100%;
  }

  input:focus {
    border-color: rgb(0, 118, 173);
  }

</style>
